:root {
  /* Font sizes */
  --fs-200: 0.875rem;
  --fs-300: 1rem;
  --fs-400: 0.9375rem;
  --fs-500: 1rem;
  --fs-600: 1rem;
  --fs-700: 1.5rem;
  --fs-800: 3.5rem;
  --fs-900: 5rem;
  /* font-families */
  --ff-serif: "Bellefair", serif;
  --ff-sans-cond: "Barlow Condensed", sans-serif;
  --ff-sans-normal: "Barlow", sans-serif;
}
@media (min-width: 35em) {
  :root {
    --fs-400: 1rem;
    --fs-500: 1.25rem;
    --fs-600: 1.5rem;
    --fs-700: 2.5rem;
    --fs-800: 5rem;
    --fs-900: 9.375rem;
  }
}
@media (min-width: 52em) {
  :root {
    --fs-400: 1.125rem;
    --fs-500: 1.75rem;
    --fs-600: 2rem;
    --fs-700: 3.5rem;
    --fs-800: 6.25rem;
  }
}

:root {
  --clr-dark: 230, 35%, 7%;
  --clr-accent: 231, 77%, 90%;
  --clr-light: 0, 0%, 100%;
}

/* ==================== */
/* Utility Classes      */
/* ==================== */
.bg-dark {
  background-color: hsl(var(--clr-dark));
}

.bg-accent {
  background-color: hsl(var(--clr-accent));
}

.bg-light {
  background-color: hsl(var(--clr-light));
}

.text-dark {
  color: hsl(var(--clr-dark));
}

.text-accent {
  color: hsl(var(--clr-accent));
}

.text-light {
  color: hsl(var(--clr-light));
}

/* ===================== */
/* Resets                */
/* ===================== */
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  font-weight: 400;
}

body,
h1,
h2,
h3,
h4,
h5,
p,
figure,
picture {
  margin: 0;
}

body {
  line-height: 1.65;
  min-height: 100vh;
  font-size: var(--fs-400);
  font-family: var(--ff-sans-normal);
  color: hsl(var(--clr-light));
  background-color: hsl(var(--clr-dark));
  display: grid;
  grid-template-rows: min-content 1fr;
  background-size: cover;
  background-position: center;
  overflow-x: hidden;
}

img,
picture {
  max-width: 100%;
}

img {
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  cursor: pointer;
  border: 0;
}

/* remove animations for people who've turned them off */
@media (prefers-reduced-motion: reduce) {
  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
/* ===================== */
/* Global Styles         */
/* ===================== */
main {
  text-align: center;
}
@media (min-width: 52em) {
  main {
    text-align: left;
  }
}

p {
  max-width: 55ch;
  color: hsl(var(--clr-light), 0.775);
}

.home {
  background-image: url("../assets/home/background-home-mobile.jpg");
}
@media (min-width: 35em) {
  .home {
    background-image: url("../assets/home/background-home-tablet.jpg");
  }
}
@media (min-width: 52em) {
  .home {
    background-image: url("../assets/home/background-home-desktop.jpg");
  }
}

.destination {
  background-image: url("../assets/destination/background-destination-mobile.jpg");
}
@media (min-width: 35em) {
  .destination {
    background-image: url("../assets/destination/background-destination-tablet.jpg");
  }
}
@media (min-width: 52em) {
  .destination {
    background-image: url("../assets/destination/background-destination-desktop.jpg");
  }
}

.crew {
  background-image: url("../assets/crew/background-crew-mobile.jpg");
}
@media (min-width: 35em) {
  .crew {
    background-image: url("../assets/crew/background-crew-tablet.jpg");
  }
}
@media (min-width: 52em) {
  .crew {
    background-image: url("../assets/crew/background-crew-desktop.jpg");
  }
}

.technology {
  background-position: center bottom;
  background-image: url("../assets/technology/background-technology-mobile.jpg");
}
@media (min-width: 35em) {
  .technology {
    background-image: url("../assets/technology/background-technology-tablet.jpg");
  }
}
@media (min-width: 52em) {
  .technology {
    background-position: right center;
    background-image: url("../assets/technology/background-technology-desktop.jpg");
  }
}

a,
a:active,
a:visited {
  text-decoration: none;
}

/* ==================== */
/* Utility Classes      */
/* ==================== */
.ff-serif {
  font-family: var(--ff-serif);
}

.ff-sans-cond {
  font-family: var(--ff-sans-cond);
}

.ff-sans-normal {
  font-family: var(--ff-sans-normal);
}

.ls-1 {
  letter-spacing: 4.75px;
}

.ls-2 {
  letter-spacing: 2.7px;
}

.ls-3 {
  letter-spacing: 2.35px;
}

.uppercase {
  text-transform: uppercase;
}

.fs-200 {
  font-size: var(--fs-200);
}

.fs-300 {
  font-size: var(--fs-300);
}

.fs-400 {
  font-size: var(--fs-400);
}

.fs-500 {
  font-size: var(--fs-500);
}

.fs-600 {
  font-size: var(--fs-600);
}

.fs-700 {
  font-size: var(--fs-700);
}

.fs-800 {
  font-size: var(--fs-800);
}

.fs-900 {
  font-size: var(--fs-900);
}

.fs-600,
.fs-700,
.fs-800,
.fs-900 {
  line-height: 1.1;
  font-weight: 400;
}

.fs-900 {
  line-height: 1;
}

.numbered-title {
  margin-top: 1rem;
  font-family: var(--ff-sans-cond);
  font-size: var(--fs-500);
  letter-spacing: 4.72px;
  text-transform: uppercase;
}

.numbered-title span {
  font-weight: 700;
  padding-right: min(1.5rem, 2vmin);
  color: hsla(var(--clr-light), 0.25);
}

.measurement {
  font-size: 1.75rem;
}

.crew__member {
  margin-bottom: min(1.6rem, 4.25vmin);
}

.crew__member-role {
  margin: min(0.9375rem, 2.25vmin) 0;
  color: hsla(var(--clr-light), 0.5);
}

/* ==================== */
/* Utility Classes      */
/* ==================== */
.flex {
  display: flex;
  gap: var(--gap, 1rem);
}

.grid {
  display: grid;
  gap: var(--gap, 1rem);
}

.d-block {
  display: block;
}

.flow > * + * {
  margin-top: var(--flow-space, 1rem);
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.container {
  padding: 0 2em;
  margin: 0 auto;
  max-width: 69.375rem;
}
@media (min-width: 52em) {
  .container {
    padding: 0;
  }
}

.container--grid {
  overflow-x: hidden;
  --gap: 2rem;
  display: grid;
  place-items: center;
}
@media (min-width: 52em) {
  .container--grid {
    align-self: end;
    padding-inline: calc(3vmax + 1rem);
    column-gap: var(--gap, 1rem);
    grid-template-columns: repeat(2, 1fr);
    /* Home Page */
  }
  .container--grid-home {
    padding-bottom: 5.625rem;
  }
  .container--grid-home > .grid-col-1 {
    grid-column: 1;
    justify-self: start;
  }
  .container--grid-home > .grid-col-2 {
    grid-column: 2;
    align-self: end;
    justify-self: end;
  }
}
@media (min-width: 90em) {
  .container--grid-home {
    padding-bottom: calc(90px + 2vmin);
  }
}

/* ==================== */
/* Destination Page     */
/* ==================== */
.container--grid-destination {
  --flow-space: 2rem;
  grid-template-areas: "title" "image" "tabs" "article";
}
.container--grid-destination .numbered-title {
  grid-area: title;
}
@media (min-width: 35em) {
  .container--grid-destination .numbered-title {
    text-align: left;
    justify-self: start;
  }
}
.container--grid-destination .destination__img {
  grid-area: image;
  max-width: 70%;
  align-self: start;
  margin-top: min(3.5rem, 4vmax);
}
.container--grid-destination .tab-list {
  gap: min(2rem, 5vmin);
  --underline-gap: 8px;
  grid-area: tabs;
}
.container--grid-destination .destination__info {
  grid-area: article;
}
.container--grid-destination .destination__meta {
  margin-top: 2.5rem;
  padding-top: 2.5rem;
  padding-bottom: 2.5rem;
  border-top: 1px solid hsla(var(--clr-light), 0.25);
  flex-direction: column;
}
@media (min-width: 35em) {
  .container--grid-destination .destination__meta {
    flex-direction: row;
    justify-content: space-evenly;
  }
}
@media (min-width: 52em) {
  .container--grid-destination .destination__meta {
    --gap: 3.5rem;
    justify-content: flex-start;
  }
}
@media (min-width: 52em) {
  .container--grid-destination {
    justify-items: start;
    align-content: start;
    grid-template-areas: "title title" "image tabs" "image article";
  }
}

/* ==================== */
/* Crew Page            */
/* ==================== */
.container--grid-crew {
  --flow-space: 2rem;
  grid-template-areas: "title" "image" "tabs" "article";
}
.container--grid-crew .numbered-title {
  grid-area: title;
}
.container--grid-crew .crew__img {
  padding-inline: 5em;
  border-bottom: 1px solid hsla(var(--clr-light), 0.5);
  grid-area: image;
}
.container--grid-crew .tab-list {
  grid-area: tabs;
}
.container--grid-crew .crew__info {
  grid-area: article;
  padding-bottom: 2em;
}
@media (min-width: 35em) {
  .container--grid-crew {
    grid-template-areas: "title" "article" "tabs" "image";
  }
}
@media (min-width: 52em) {
  .container--grid-crew {
    align-items: end;
    justify-items: start;
    grid-template-columns: minmax(0, 38.4375rem) minmax(0, 20.9375rem);
    grid-template-areas: "title image" "article image" "tabs image";
  }
  .container--grid-crew .tab-list {
    padding-bottom: min(5.7rem, 5vmax);
  }
  .container--grid-crew .crew__img {
    border: 0;
    padding: 1rem 0 0;
  }
}

/* ==================== */
/* Technology Page      */
/* ==================== */
.container--grid-tech {
  --flow-space: 2rem;
  grid-template-areas: "title" "image" "tabs" "article";
}
.container--grid-tech .numbered-title {
  grid-area: title;
}
.container--grid-tech .tech__img {
  grid-area: image;
}
.container--grid-tech .tab-list {
  grid-area: tabs;
}
.container--grid-tech .tech__info {
  padding-bottom: min(1rem, 3vw);
  grid-area: article;
}
.container--grid-tech .tech__info p:last-child {
  margin-top: min(1em, 3vmin);
}
@media (min-width: 52em) {
  .container--grid-tech {
    padding: 0;
    margin: 0;
    min-width: 100%;
    padding-bottom: min(6rem, 6vw);
    justify-items: start;
    grid-template-columns: minmax(1rem, 1fr) min-content repeat(2, minmax(0, 555px)) minmax(1rem, 1fr);
    grid-template-areas: ". title title title ." ". tabs article image image" ". tabs article image image";
  }
  .container--grid-tech .tech__img {
    justify-self: end;
  }
  .container--grid-tech .tab-list {
    flex-direction: column;
  }
}

.header {
  position: relative;
  align-items: center;
  justify-content: space-between;
  padding-top: 1.5em;
  padding-left: 1.5em;
}
@media (min-width: 35em) {
  .header {
    padding-top: 0;
    padding-left: 2.5em;
  }
}
@media (min-width: 52em) {
  .header {
    padding-top: 2.5em;
    padding-left: 3.4375em;
  }
}

@media (max-width: 34.9375em) {
  .nav__list--item.indicator--underline {
    border: 0;
    padding-block: 11px;
  }
  .nav__list--item.indicator--underline.active {
    border-right: 4px solid hsl(var(--clr-light));
  }
}
.nav__list {
  --gap: min(2.3125rem, 5vw);
  padding: 0;
  background: hsla(var(--clr-dark), 0.9);
}
@media (max-width: 34.9375em) {
  .nav__list {
    --gap: 1.5625rem;
    padding: 6.25rem 0 18.75rem 2em;
    flex-direction: column;
    position: fixed;
    inset: 0 0 0 31.5%;
    z-index: 99;
    transform: translateX(100%);
    transition: transform 350ms ease-in-out;
  }
  .nav__list[data-visible=true] {
    transform: translateX(0);
  }
}
@media (min-width: 35em) {
  .nav__list {
    padding-inline: clamp(2rem, 4vw, 7rem);
  }
}
@media (min-width: 52em) {
  .nav__list {
    --gap: min(3rem, 4.5vw);
  }
}
.nav__link > span {
  font-weight: 700;
  padding-right: min(10px, 1vmin);
}
@media (min-width: 35em) and (max-width: 51.9375em) {
  .nav__link > span {
    display: none;
  }
}

@supports (backdrop-filter: blur(2px)) {
  .nav__list {
    background: hsla(var(--clr-light), 0.04);
    backdrop-filter: blur(27px);
  }
}
.btn-large {
  width: 100%;
  min-width: 150px;
  max-width: 150px;
  aspect-ratio: 1;
  border-radius: 50%;
  display: inline-grid;
  place-items: center;
  position: relative;
  z-index: 1;
  font-size: 1.25rem;
  letter-spacing: 1.25px;
}
@media (min-width: 35em) {
  .btn-large {
    max-width: 242px;
    font-size: 1.625rem;
  }
}
@media (min-width: 52em) {
  .btn-large {
    max-width: 274px;
    margin-right: 2em;
    font-size: 2rem;
  }
  .btn-large::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    opacity: 0;
    border-radius: 50%;
    background-color: hsla(var(--clr-light), 0.15);
    transition: transform 300ms ease-out, opacity 350ms ease-in-out;
  }
  .btn-large:hover::after, .btn-large:focus::after {
    opacity: 1;
    transform: scale(1.5);
  }
}

.nav-toggle {
  display: none;
}
@media (max-width: 34.9375em) {
  .nav-toggle {
    display: block;
    position: absolute;
    top: 50%;
    right: 1.5rem;
    z-index: 100;
    width: 24px;
    height: 24px;
    background-color: transparent;
    background-repeat: no-repeat;
    background-position: center;
    background-image: url("../assets/shared/icon-hamburger.svg");
    transform: translateY(-50%);
  }
  .nav-toggle[aria-expanded=true] {
    background-image: url("../assets/shared/icon-close.svg");
  }
  .nav-toggle:focus-visible {
    outline: 2px solid #D0D6F9;
    outline-offset: 2px;
  }
}

.skip-to-content {
  position: absolute;
  z-index: 9999;
  background: hsl(var(--clr-light));
  color: hsl(var(--clr-dark));
  padding: 0.5em 1em;
  margin-inline: auto;
  transform: translateY(-100%);
  transition: transform 250ms ease-in;
}

.skip-to-content:focus {
  transform: translateY(0);
}

.indicator--disc,
.indicator--numbered {
  aspect-ratio: 1;
  border-radius: 50%;
}

/* ===================== */
/* Underline Style       */
/* ===================== */
.indicator--underline {
  cursor: pointer;
  padding: var(--underline-gap, 2.25rem) 0;
  border-bottom: 3px solid hsla(var(--clr-light), 0);
}
.indicator--underline:hover, .indicator--underline:focus {
  border-color: hsla(var(--clr-light), 0.5);
}
.indicator--underline.active, .indicator--underline[aria-selected=true] {
  color: hsl(var(--clr-light));
  border-color: hsla(var(--clr-light), 1);
}

/* ===================== */
/* Dot Style             */
/* ===================== */
.indicator--disc {
  width: 15px;
  background-color: hsla(var(--clr-light), 0.25);
}
.indicator--disc:hover, .indicator--disc:focus {
  background-color: hsla(var(--clr-light), 0.5);
}
.indicator--disc:focus {
  outline: 2px solid hsl(var(--clr-light));
  outline-offset: 2px;
}
.indicator--disc[aria-selected=true] {
  background-color: hsla(var(--clr-light), 1);
}

/* ===================== */
/* Numbered Style        */
/* ===================== */
.indicator--numbered {
  width: 80px;
  color: hsl(var(--clr-light));
  border: 1px solid hsla(var(--clr-light), 0.25);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: var(--fs-600);
  background: transparent;
}
.indicator--numbered:hover, .indicator--numbered:focus {
  border-color: hsla(var(--clr-light), 1);
}
.indicator--numbered:focus {
  outline: 2px solid hsl(var(--clr-light));
  outline-offset: 2px;
}
.indicator--numbered[aria-selected=true] {
  color: hsl(var(--clr-dark));
  background-color: hsla(var(--clr-light), 1);
}

/* ===================== */
/* Tabs                  */
/* ===================== */
.tab__list {
  --gap: 3vmin;
}

.tab-list > button.indicator--underline {
  background: transparent;
  justify-self: start;
}

.tab-panel {
  display: block;
  opacity: 1;
  transform: translateX(0);
  transition: transform 450ms ease-in-out, opacity 350ms linear;
}
.tab-panel[hidden] {
  opacity: 0;
  pointer-events: none;
  transform: translateX(100%);
}

.tab-panel--crew[hidden],
.tab-panel--tech[hidden] {
  transform: translateX(-100%);
}
@media (max-width: 51.9375em) {
  .tab-panel--crew[hidden],
.tab-panel--tech[hidden] {
    transform: translateX(100%);
  }
}

.tab-panel__img {
  display: block;
  opacity: 1;
  transform: translateX(0);
  transition: transform 450ms ease-in-out, opacity 350ms linear;
}
.tab-panel__img[hidden] {
  opacity: 0;
  transform: translateX(100%);
}
.tab-panel__img--destination[hidden] {
  transform: translateX(-100%);
}
@media (max-width: 51.9375em) {
  .tab-panel__img--destination[hidden] {
    transform: translateX(100%);
  }
}

.line {
  height: 1px;
  width: 100%;
  position: absolute;
  top: 17%;
  left: 17%;
  right: 0;
  background: hsla(var(--clr-light), 0.25);
}
@media (max-width: 51.9375em) {
  .line {
    display: none;
  }
}